body {
  margin: 0;
  padding: 0;
  line-height: inherit;
  background-color: #2A2D32;
  color: #E7E8E9;
  font-family: "Play", sans-serif;
}

.our-placeholder-block {
  padding: 20px;
  text-align: center;
  font-size: 1.65rem;
  background: linear-gradient(180deg, #dedede, #adadad);
}

.read-more {
  text-align: center;
  margin: 20px;
}
.read-more .read-more-button {
  text-decoration: none;
  backdrop-filter: blur(13px) saturate(200%);
  -webkit-backdrop-filter: blur(13px) saturate(200%);
  background-color: rgba(231, 232, 233, 0.26);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.125);
  padding: 16px;
  color: #14e8ff;
}